//header
header {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 180px;
  background: #26254f;
  padding: 20px 0;
  z-index: 9;

  img{
    width: 130px;
    height: auto;
  }

  ul {
    width: 100%;
    margin: 0;
    padding-left: 8px;

    li {
      position: relative;
      height:118px;
      padding: 24px 0;
      margin-top: -24px;
      margin-bottom: -24px;

      &:first-child > a {
        color: #D53548;
      }

      &:nth-child(2) > a {
        color: #76E449;
      }

      &:nth-child(3) > a {
        color: #407AF4;
      }

      &:nth-child(4) > a {
        color: #7420AC;
      }

      &:last-child > a {
        color: #FF6525;
      }

      &.btn-entrance > a {
        color: #ffffff;
      }

      a {
        position: relative;
        font-family: "MontRegular", sans-serif;
        width: 100%;
        height: 100%;
        border-radius: 35px 0 0 35px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding-left: 15px;

        img {
          width: 32px;
          height: auto;
          margin-right: 15px;
        }
      }
    }

    li.active {
      background: linear-gradient(to right, #26254f 50%, #1e1d40 50%);

      &:before {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        width: 50%;
        height: 24px;
        background: #26254f;
        border-radius: 0 0 24px 0;
      }

      &:after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        width: 50%;
        height: 24px;
        background: #26254f;
        border-radius: 0 24px 0 0;
      }

      a {
        background: #1e1d40;
      }
    }
  }
}
